.web-loading {
  background-color: #ffffff;
  height: 100vh;
  display: inline-flex;
  width: 100vw;
  justify-content: center;
  align-items: center;
}

.web-loading .loading {
  align-items: center;
  display: inline-flex;
  flex-direction: column;
  gap: 20vh;
  position: relative;
}

.web-loading .RZD-logo-st-neg-cmyk-1 {
  height: 161px !important;
  position: relative !important;
  width: 220px !important;
}

.web-loading .div {
  align-items: center;
  display: inline-flex;
  flex: 0 0 auto;
  flex-direction: column;
  gap: 25px;
  position: relative;
}

.web-loading .rectangle-wrapper {
  background-color: #dadada;
  border-radius: 20px;
  box-shadow: 0px 2px 7px #00000059;
  height: 2vh;
  position: relative;
  width: 80vw;
  display: inline-flex;
  justify-content: flex-start;
  overflow: hidden;
}

.web-loading .rectangle {
  background-color: #242424;
  border-radius: 20px;
  height: 2vh;
  transition: width 0.3s ease-in-out;
}

.web-loading .text-frame {
  align-items: center;
  display: inline-flex;
  flex: 0 0 auto;
  justify-content: center;
  position: relative;
}

.web-loading .text {
  color: #242424;
  font-family: "Raleway", Helvetica;
  font-size: 24px;
  font-weight: 800;
  height: 35px;
  letter-spacing: 0;
  line-height: normal;
  margin-top: -1px;
  position: relative;
  text-align: center;
  width: 100vw;
}