.audio-pop-up {
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.6);
  }

  .audio-pop-up .content{
    background-color: #fff;
    width: 250px;
    padding: 3vh;
    border-radius: 20px;
    position: absolute;
    gap: 2vh;
    display: inline-flex;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    flex-direction: column;
  }
  
  .audio-pop-up .content .text-wrapper {
    font-family: "TT Norms Pro-Regular", Helvetica;
    font-weight: 400;
    color: #000000;
    position: relative;
    width: fit-content;
    margin-top: -1px;
    text-align: center;
    letter-spacing: 0;
    line-height: normal;
  }
  
  .audio-pop-up .content .button-accept {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    padding: 4vw, 4vw, 4vw;
    position: relative;
    align-self: stretch;
    width: 100%;
    background-color: #1c1c1c;
    border-radius: 100px;
  }
  
  .audio-pop-up .content .div {
    font-family: "TT Norms Pro-Bold", Helvetica;
    font-weight: bold;
    color: #ffffff;
    white-space: nowrap;
    padding: 1.5vh;
    position: relative;
    width: fit-content;
    margin-top: -1px;
    text-align: center;
    letter-spacing: 0;
    line-height: normal;
  }
